.contact-details {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    .contact-section {
        .user-img-wrapper {
            display: flex;
            flex-direction: column;
            align-items: center;
            .contact-img {
                background-color: $clr6;
                border-radius: 1rem;
                width: 30vw;
                margin-bottom: 0.6rem;
            }
            h1 {
                margin-bottom: 0.6rem;
            }
            .btns-sec {
                display: flex;
                flex-direction: row;
                .icon-btn {
                    margin: 0 1rem;
                    &:nth-child(2) {
                        margin: 0;
                    }
                }
            }
        }
        .info-section {
            display: flex;
            flex-direction: column;
        }
    }
    .btns-sec {
        display: flex;
        flex-direction: column;
    }
}